import React, { Component } from 'react'

import { RefreshRateMsOptions } from './RefreshRateMsOptions'

import Select from 'react-select'

export default class RefreshRateMsSetting extends Component {

  constructor(props) {
    super(props)

    this.options = []
    Object.keys(RefreshRateMsOptions).map(key => this.options.push(RefreshRateMsOptions[key]))
  }

  handleChange = selectedOption => {
    this.props.setRefreshRateMs(selectedOption.value)
  }

  customStyles = {
    option: (provided, state) => ({
      ...provided,
      textAlign: "right",
    }),
    control: (provided) => ({
      ...provided,
    }),
    singleValue: (provided, state) => ({
      ...provided,
    }),
  }

  render() {
    const selectedOption = RefreshRateMsOptions[this.props.refreshRateMs.toString()]

    return (
      <div style={{width: 150}}>
        <Select
          styles={this.customStyles}
          value={selectedOption}
          onChange={this.handleChange}
          options={this.options}
        />
      </div>
    )
  }
}
