import React from 'react'
import { fromJS } from 'immutable-sorted'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import './AddConnection.css'

type Props = {
  addConnection: (connection: any) => void
}

export default (props: Props) => {
  const {addConnection} = props
  let input: ?{value: string}

  const onSubmit = e => {
    e.preventDefault()
    if (!input || !input.value.trim()) {
      return
    }
    addConnection(fromJS({url: input.value, state: 'created', message: ''}))
    input.value = ''
  }

  return (
    <form onSubmit={onSubmit}>
    <input className="AddConnection-input" ref={node => {input = node}} />
    <button className="AddConnection-input" type="submit">
    <FontAwesomeIcon icon="plus"/>
    </button>
    </form>
  )
}
