import { connect } from 'react-redux'

import { engine /*, getState */} from '../../engine'

const e = engine.subEngine('home')
// const af = e.actionFactory();
const ce = engine.subEngine('connections')
const caf = ce.actionFactory();
const se = engine.subEngine('settings')
// const saf = se.actionFactory();

const mapStateToProps = (state, cState, sState, ownProps) => ({
  devMode: sState.get("devMode"),
  runsCnt: cState.get("runsCnt"),
  runId: cState.get("runId"),
  runs: cState.get("runsView"),
  keys: cState.get("keys"),
})

const mapRepoToProps = (repo, ownProps) => mapStateToProps(e.get(e.head(repo)), ce.get(ce.head(repo)), se.get(se.head(repo)), ownProps)

const mapDispatchToProps = (dispatch, ownProps) => ({
  setRunId: runId => dispatch(caf.value("runId", runId))
})

const HomeContainer = connect(
  mapRepoToProps,
  mapDispatchToProps
)

export default HomeContainer
