import React, { Component } from 'react'

import RunsSetting from './components/RunsSetting'

// import css from './HomePage.module.css'
import { List } from 'immutable-sorted'

import '../Table/Table.css'

import ReactJson from 'react-json-view'

const errSort = (a, b) => b.get("twsTimeMs") - a.get("twsTimeMs")

export default class HomePage extends Component {
  renderCnt = 0

  render() {
    const { props } = this
    const { runId, runs, keys, setRunId } = props
    const hostname = window.location.hostname
    const errors = keys.get("0:TwsErrorsLog::") || List()
    const runErrors = errors.filter(rec => rec.get("runId") === runId).sort(errSort)
    const frun = runs.filter(value => value.get("runId") === runId)
    this.renderCnt++
    return (
    <div>
      <h3>Server {hostname}</h3>
      {props.devMode?<b>RenderCnt: {this.renderCnt}</b>:null}
      <p/>
      <h3>Runs ({props.runsCnt})</h3>
      <p/>
      <table style={{maxWidth:"100%"}} className="minimalistBlack">
        <thead>
          <tr>
            <td colSpan={5}>
              <RunsSetting runs={runs} runId={runId} setRunId={setRunId} />
            </td>
          </tr>
          <tr>
            <th>RunID</th>
            <th>Time</th>
            <th>Type</th>
            <th>Status</th>
            <th>Error</th>
          </tr>
        </thead>
        <tbody>
        {frun.map((value, key)=><tr key={key}>
          <td>{value.get("runId")}</td>
          <td>{value.get("time")}</td>
          <td>{value.getIn(["options", "replayMode"])?"replay":"real time"}</td>
          <td>{value.get("status")}</td>
          <td>{value.get("error")}</td>
        </tr>).toSetSeq()}
        </tbody>
      </table>
      <p/>
      <h3>Config</h3>
      <p/>
      <table style={{maxWidth:"100%"}} className="minimalistBlack">
      <tbody>
      {frun.valueSeq().map((run, i) => <tr key={i}><td><ReactJson name={false} collapsed={2} src={run.toJS()} /></td></tr>)}
      </tbody>
      </table>
      <p/>
      <h3>Errors ({runErrors.size})</h3>
      {/* <ReactJson  style={{textAlign:"left"}} name={false} collapsed={2} src={errors?errors.sort(errSort).toJS():{}} /> */}
      <table style={{maxWidth:"100%"}} className="minimalistBlack">
        <thead>
          <tr>
            <th>RunId</th>
            <th>Time</th>
            <th>Type</th>
            <th>Subscription</th>
            <th>Error</th>
          </tr>
        </thead>
        <tbody>
        {runErrors.map((e, key) => {
          const sub = e.get("subscription")
          return <tr key={key}>
            <td>{e.get("runId")}</td>
            <td>{e.get("twsTime")}</td>
            <td>{e.get("type")}</td>
            <td>{sub && sub.getIn(["contract", "name"])}{sub && ":"+sub.getIn(["item"])+""}{sub && " ("+sub.getIn(["contract", "secType"])+"-"+sub.getIn(["contract", "exchange"])+"-"+sub.getIn(["contract", "currency"])+")"}</td>
            <td style={{maxWidth: 600}}>{e.get("errorMsg").substr(0, 200).replace(/\//g, " / ")}</td>
          </tr>
        }).toSetSeq()}
        </tbody>
      </table>
    </div>
    )
  }
}
