import React, { Component } from 'react';
import { Map } from 'immutable-sorted'

import { AddConnection } from './components'

import type { Connection } from 'xbroker';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import './ConnectionsPage.css'

type Props = {
  connections: Map<number, Connection>,
  connectionsCnt: Map<string, number>,
  addConnection: (connection: Connection) => void,
  removeConnection: (id: string) => void
}

export default class ConnectionsPage extends Component<Props> {
  removeConnection(id: string) {
    this.props.removeConnection(id)
  }

  renderStateIcon(state: string) {
    var icon
    switch(state) {
      case 'open': {
        icon = <FontAwesomeIcon color="blue" icon="wifi" title="Open"/>
        break
      }
      default: {
        icon = <FontAwesomeIcon color="red" icon="exclamation-triangle" title="Remove"/>
        break
      }
    }
    return icon
  }

  renderConnections(connections: Map<string, Connection>) {
    if(!connections) {
      return null;
    }

    // const style = "blueTable"
    const style = "minimalistBlack"
    
    return <div>
      <table className={style}>
        <thead>
        <tr>
          <th>Name</th>
          <th>URL</th>
          <th>Status</th>
          <th>Delete</th>
        </tr>
        </thead>
        <tbody>
      {connections.map((connection: Connection, name: string) => {
        const state: string = connection.get("state")
        return (<tr key={name}>
          <td>{name}</td>
          <td><b>{connection.get("url")}<br/>{connection.get("message")}</b></td>
          <td className="center">{this.renderStateIcon(state)}</td>
          <td className="center"><button onClick={this.removeConnection.bind(this, name)}><FontAwesomeIcon icon="trash-alt" title="Remove"/></button></td>
          </tr>)
      }
      ).toIndexedSeq()}
        </tbody>
      </table>
      </div>;
  }

  render() {
    const { connections, connectionsCnt, addConnection } = this.props;

    return (
      <div>
        <header>
          <h2>Connections ({connectionsCnt.get("cnt")})</h2>
        </header>
        <AddConnection addConnection={addConnection} />
        <br/>
        <div>
          {this.renderConnections(connections)}
        </div>
      </div>
    );
  }
}
