import type { Schema } from 'duxen'

import { Seq } from 'immutable-sorted'

const schema:Schema = {

  connectionId: {
    type: 'value',
    persistent: true,
    initValue: 1,
  },

  connections: {
    type: 'collection',
    persistent: true,
  },

  'connectionsCnt': {
    type:'view',
    sourceName: 'connections',
    props: [],
    recipe: (seq, props)=>Seq({cnt: seq.count()})
  },

  error: {
    type: 'value',
    persistent: false,
    initValue: null,
  },

  runId: {
    type: 'value',
    persistent: false,
    initValue: "0",
  },
 
  runs: {
    type: 'collection',
    persistent: false,
  },

  runsCnt: {
    type:'formula',
    props: ["runs"],
    recipe: (props)=>props.runs.count()
  },

  runsView: {
    type: 'view',
    sourceName: 'runs',
    props: [],
    recipe: (seq, props)=>seq.sort((a, b)=>{
      const r1 = b.get("runId")
      if(r1) {
        const r2 = a.get("runId")
        return parseInt(r1) - parseInt(r2)
      }
      return 0
    }),
  },

  runsLog: {
    type: 'collection',
    persistent: false,
  },

  keys: {
    type: 'collection',
    persistent: false,
  },

  keysCnt: {
    type:'formula',
    props: ['keys'],
    recipe: (props)=>props.keys.count()
  },

  messages: {
    type: 'collection',
    persistent: false,
  },

  'messagesView': {
    type:'view',
    sourceName: 'messages',
    props: [],
    recipe: (seq, props)=>seq,
    recipe0: (seq, props)=>seq.sort((a, b)=>{
        const a1 = a.get("componentName")
      if(a1) {
        const c1 = a1.localeCompare(b.get("componentName"))
        if(c1 !== 0) {
          return c1
        }
      }
      const a2 = a.getIn(["common", "contract", "name"])
      if(a2) {
        const c2 = a2.localeCompare(b.getIn(["common", "contract", "name"]))
        if(c2 !== 0) {
          return c2
        }
      }
      const a3 = a.getIn(["common", "item"])
      if(a3) {
        const c3 = a3.localeCompare(b.getIn(["common", "item"]))
        if(c3 !== 0) {
          return c3
        }
      }
      return 0
    }),
  },

}

export default schema
