import { connect } from 'react-redux'

import { engine } from '../../engine'

import { reload } from '../Connections/ConnectionsContainer'

const e = engine.subEngine('settings')
const af = e.actionFactory();

const mapStateToProps = (state, repoHead, ownProps) => ({
  dataBars: state.get("dataBars"),
  displayBars: state.get("displayBars"),
  subscriptionPattern: state.get("subscriptionPattern"),
  refreshRateMs: state.get("refreshRateMs"),
  devMode: state.get("devMode"),
})

const mapRepoToProps = (repo, ownProps) => mapStateToProps(e.get(e.head(repo)), e.head(repo), ownProps)
 
const mapDispatchToProps = (dispatch, ownProps) => ({
  setDataBars: (dataBars) => {
    dispatch(af.value("dataBars", dataBars))
  },

  setDisplayBars: (displayBars) => {
    dispatch(af.value("displayBars", displayBars))
  },

  setSubscriptionPattern: (subscriptionPattern) => {
    dispatch(af.value("subscriptionPattern", subscriptionPattern))
    reload()
  },

  setRefreshRateMs: (refreshRateMs) => {
    dispatch(af.value("refreshRateMs", refreshRateMs))
  },

  setDevMode: (checked) => {
    dispatch(af.value("devMode", checked))
  },
})

const SettingsContainer = connect(
  mapRepoToProps,
  mapDispatchToProps
)

export default SettingsContainer
