import React, { Component } from 'react'

import Select from 'react-select'

export default class RunsSetting extends Component {

  constructor(props) {
    super(props)
    this.state = {
      options: [],
      optionsMap: {},
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const { runs } = this.props

    if(prevProps.runs !== runs) {
      const options = []
      const optionsMap = {}
  
      runs.forEach((value, key) => {
        const option = {value: value.get("runId"), label: "RunId: " + value.get("runId") + " - " + value.get("time") + ", " + (value.getIn(["options", "replayMode"])?"replay":"real time") + ", " + value.get("status")}
        options.push(option)
        optionsMap[key] = option
      })

      this.setState({options, optionsMap})
    }
  }

  handleChange = selectedOption => {
    this.props.setRunId(selectedOption.value)
  }

  render() {
    const { options, optionsMap } = this.state
    const selectedOption = optionsMap[this.props.runId] || options[0]

    return (
      <span style={{width: 150}}>
        <Select 
            value={selectedOption}
            onChange={this.handleChange}
            options={options}
        />
      </span>
    )
  }
}
