import React, { Component } from 'react'
import { isImmutable } from 'immutable-sorted'
import PropTypes from 'prop-types'
import ReactJson from 'react-json-view'

import './TimeMachinePage.css'

export default class TimeMachinePage extends Component {

  actionToJs(action) {
    let copy = {};
    let key;
  
    for (key in action) {
      let value = action[key];
      if(isImmutable(value)) {
        value = value.toJS();
      }
      copy[key] = value;
    }
    return copy;
  }

  preRender() {

    const { state, action, repo, showRepo } = this.props;

    return (
      <div>
        <header>
          <h2>State Inspector</h2>
        </header>
        <div className="TimeMachinePage-body">
        {showRepo
        ?[
          <h4 key={1}>Repository</h4>,
          <ReactJson key={2} name={false} src={repo.toJS()} />]
        :[
        <h4 key={1}>Action</h4>,
        <ReactJson key={2} name={false} collapsed={1} src={this.actionToJs(action)} />,
        <h4 key={3}>State</h4>,
        <ReactJson key={4} name={false} collapsed={2} src={state.toJS()} />]
        }
        </div>
      </div>
    )
  }

  render() {
    const { live } = this.props;

    if(!live || !this.pre) {
      this.pre = this.preRender()
    }

    return this.pre
  }
}

TimeMachinePage.propTypes = {
  state: PropTypes.object,
  prevState: PropTypes.object,
  action: PropTypes.object,
}
